<template>
  <!-- 淨水廠頁面 -->
  <div class="cleanwater">
    <!-- 標題 -->
    <div class="row text-center" style="margin: 0 15px 0 15px">
      <div class="col-12 col-xl-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >淨水廠區</span
        >
      </div>
    </div>
    <div
      class="animate__animated animate__backInLeft childtitle one"
      style="animation-duration: 1s; animation-iteration-count: 1"
    >
      你知道嗎?
    </div>
    <div>
      自來水產生的過程是水源經過取水口取水，藉由原水幹管輸送至淨水場，原水經過混凝、沉澱、過濾、消毒等淨水處理程序成為清水，再經加壓站及輸配管線送至用戶住家。
    </div>
    <!-- 淨水過程圖片 -->
    <el-row class="imgRow">
      <el-col
        class="imgPosition"
        :xs="6"
        :span="3"
        v-for="item in imgArray"
        :key="item.src"
      >
        <img
          :src="item.src"
          class="img-fluid@iassets/img/mg-respo@sassets/img/ive animate__animated animate__fadeInLeft"
          alt="Responsive image"
          :style="{
            animationDuration: '1s',
            animationIterationCount: '1',
            animationDelay: item.delay,
          }"
        />
      </el-col>
    </el-row>
    <el-row class="imgExplainRow" :gutter="30">
      <el-col
        :xs="12"
        :span="3"
        v-for="item in cleanWaterExplain"
        :key="item.title"
      >
        <el-card class="box-card" :body-style="{ background: '#ECF7FA' }">
          <div slot="header" class="clearfix">
            <span>{{ item.title }}</span>
          </div>
          <div>
            {{ item.content }}
          </div>
        </el-card>
      </el-col>
    </el-row>
    <div class="infoFrom">
      資料來源：<a href="https://www3.water.gov.tw/ch/10visit/visit_02.asp"
        >台灣自來水公司自來水淨水流程說明</a
      >
    </div>
    <hr />
    <br />

    <!-- 各淨水廠監測資訊 -->
    <div class="row text-center" style="margin: 0 15px 0 15px">
      <div class="col-12 col-xl-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >各淨水廠監測資訊</span
        >
      </div>
    </div>
    <!-- 地圖  -->
    <el-row :gutter="30">
      <el-col :xs="24" :span="10">
        <l-map class="map" :zoom="zoom" :options="options" :center="center">
          <!-- 載入地圖資料 -->
          <l-tile-layer :url="url" :attribution="attribution" />
          <!-- 地標 -->
          <l-marker
            ref="location"
            :lat-lng="item.local"
            v-for="item in MapData"
            :key="item.name"
            @click="tableHighlight(item.name)"
          >
            <!-- 地標圖示 -->
            <l-icon :icon-url="iconimg" />
            <!-- 點擊圖標會彈出顯示的文字 -->
            <l-popup>{{ item.name }}</l-popup>
          </l-marker>
        </l-map>
      </el-col>
      <!-- 淨水廠資訊 -->
      <el-col :xs="24" :span="14">
        <el-collapse
          v-model="activeNames"
          @change="handleChange"
          class="collapseBlock"
          v-if="getDataFinished"
        >
          <el-collapse-item
            :name="index"
            v-for="(item, index) in cleanwaterData"
            :key="item.name"
          >
            <template slot="title">
              {{ item.name }}
              <div class="pass">是否通過檢驗 : {{ item.pass }}</div>
            </template>
            <el-table :data="tableData[index]" stripe fit class="dataTable">
              <el-table-column prop="type" label="種類"></el-table-column>
              <el-table-column prop="dirty" label="濁度"></el-table-column>
              <el-table-column prop="ph" label="PH值"></el-table-column>
              <el-table-column prop="ceil" label="大腸桿菌值"></el-table-column>
            </el-table>
            <div style="margin-left: 70%; font-size: 14px">
              更新時間 : {{ tableData[0][0].updatetime }}
            </div>
          </el-collapse-item>
        </el-collapse>
      </el-col>
    </el-row>
    <div class="footer"></div>
  </div>
</template>
<script>
export default {
  mounted() {
    this.getcleanwater();
  },
  methods: {
    // 根據地圖上點哪地標，將對應資料移動到右側圖表最上面並展開
    tableHighlight(value) {
      console.log(value);
      this.cleanwaterData = [];
      this.tableData = [];
      this.cleanwaterData = this.originalData.filter((item) => {
        return item.name !== value;
      });
      let keyItem = this.originalData.filter((item) => {
        return item.name === value;
      });
      this.cleanwaterData.splice(0, 0, ...keyItem);
      console.log(this.cleanwaterData);
      this.cleanwaterData.forEach((item) => {
        let obj = [
          {
            type: "測值",
            dirty: item.dirty,
            ceil: item.ceil,
            ph: item.ph,
            updatetime: item.updatetime,
          },
          {
            type: "最大限值",
            dirty: this.cleanwaterStandeardData[0].dirtystandard,
            ceil: this.cleanwaterStandeardData[0].ceilstandard,
            ph: this.cleanwaterStandeardData[0].phstandard,
          },
        ];
        this.tableData.push(obj);
      });
    },
    async getcleanwater() {
      let data = require("@/file/cleanwater2.json");
      if (data.length) {
        this.originalData = data;
        this.cleanwaterData = this.originalData;
      }
      let standardData = require("@/file/cleanwaterStandard.json");
      if (data.length) {
        this.cleanwaterStandeardData = standardData;
      }
      this.cleanwaterData.forEach((item) => {
        let obj = [
          {
            type: "測值",
            dirty: item.dirty,
            ceil: item.ceil,
            ph: item.ph,
            updatetime: item.updatetime,
          },
          {
            type: "最大限值",
            dirty: this.cleanwaterStandeardData[0].dirtystandard,
            ceil: this.cleanwaterStandeardData[0].ceilstandard,
            ph: this.cleanwaterStandeardData[0].phstandard,
          },
        ];
        this.tableData.push(obj);
      });
      console.log(this.tableData);
      this.getDataFinished = true;
    },
    handleChange(val) {
      console.log(val);
    },
  },
  data() {
    return {
      imgArray: [
        { src: require("@/assets/img/smallWater.png"), delay: "0" },
        { src: require("@/assets/img/pool1.png"), delay: "1s" },
        { src: require("@/assets/img/pool2.png"), delay: "1.5s" },
        { src: require("@/assets/img/pool3.png"), delay: "2s" },
        { src: require("@/assets/img/pool4.png"), delay: "2.5s" },
        { src: require("@/assets/img/pool5.png"), delay: "3s" },
        { src: require("@/assets/img/pool6.png"), delay: "3.5s" },
        { src: require("@/assets/img/pool7.png"), delay: "4s" },
      ],
      cleanWaterExplain: [
        { title: "原水", content: "指得是水庫、地表水或地下水" },
        {
          title: "分水井",
          content:
            "將原水分配至淨水設備並具有安定原水之水位，量測原水流量之功能。",
        },
        {
          title: "快混池",
          content:
            "為吸附及凝聚原水中懸浮固體、於原水中加入PAC等混凝劑，藉由各種形式之攪拌（重力流水躍式或電動攪拌機）達成快速混合作用。",
        },
        {
          title: "膠凝池(慢混池)",
          content:
            "快混後（加藥混合）之原水，產生膠羽（混凝劑吸附水中懸浮固體）顆粒，藉由快逐漸變慢速度之膠凝機攪拌，以膠凝作用使小膠羽逐漸結合較大且重之膠羽。",
        },
        {
          title: "沉澱池",
          content:
            "已形成膠羽之原水，經足夠滯留時間藉重力之沉降作用、分離原水中較大之膠羽顆粒及懸浮固體。",
        },
        {
          title: "快濾池",
          content:
            "將沉澱後原水中之細小懸浮物質及微生物等藉由濾料之隔除、沉澱、吸附及化學反應等作用加以去除，並藉反沖洗機制維持濾料之清潔。",
        },
        {
          title: "清水池",
          content:
            "快濾後之清水經加氯消毒進入清水池，主要功能係維持淨水場保持穩定出水量之調節能力及確保加氯消毒停留時間、增加消毒效果。",
        },
        {
          title: "供水區",
          content: "打開水龍頭就有乾淨的水可以使用了!!",
        },
      ],
      getDataFinished: false,
      tableData: [],
      cleanwaterStandeardData: [],
      cleanwaterData: [],
      originalData: [],
      activeNames: [0],
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 9,
      //地圖中心
      center: [22.912786, 120.442959],
      // 地標圖示
      iconimg: require("leaflet/dist/images/marker-icon.png"),
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      // ---------------------地圖打點資料---------------------
      MapData: [
        {
          name: "寶來淨水場",
          local: [23.1075319, 120.700922],
        },
        {
          name: "手巾寮淨水場",
          local: [22.8825185, 120.4955405],
        },
        {
          name: "新威淨水場",
          local: [23.0026195, 120.6545022],
        },
        {
          name: "茂林淨水場",
          local: [22.887452, 120.662895],
        },
        {
          name: "美濃淨水場",
          local: [22.888106, 120.536352],
        },
        {
          name: "多納淨水場",
          local: [22.9104378, 120.7154531],
        },
        {
          name: "民生淨水場",
          local: [23.2669093, 120.7206086],
        },
        {
          name: "木梓淨水場",
          local: [23.0179331, 120.5534823],
        },
        {
          name: "六龜淨水場",
          local: [22.9928944, 120.6390033],
        },
        {
          name: "甲仙淨水場",
          local: [23.0870219, 120.589685],
        },
        {
          name: "拷潭淨水場",
          local: [22.5912988, 120.4000535],
        },
        {
          name: "坪頂淨水場",
          local: [22.6800953, 120.4084894],
        },
        {
          name: "鳳山淨水場",
          local: [22.5461349, 120.3910758],
        },
        {
          name: "澄清湖淨水場",
          local: [22.6519623, 120.3495696],
        },
      ],
    };
  },
};
</script>
<style lang="scss" scoped>
