    img {
      max-width: 75%;
      height: 53px;
    }
    /deep/.el-col-14 {
      width: 100%;
    }
    /deep/.el-col-10 {
      width: 100%;
    }
    /deep/ .el-collapse-item__header {
      font-size: 14px;
    }
    /deep/.cell {
      font-size: 14px;
      line-height: 1422px;
    }
    .collapseBlock {
      height: 300px;
      /deep/ .el-collapse-item__header {
        font-size: 14px;
      }
      /deep/.cell {
        font-size: 14px;
